import React, { useEffect } from "react";
import calendar from "../../../assets/images/news/calendar.svg";
import { Container, makeStyles, Typography, Box } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { getNewsById } from "../../../redux/reducers/infoReducer";
import { useParams } from "react-router-dom";

const DetailNews = ({ match }) => {
  const dispatch = useDispatch();
  const { id } = useParams();
  useEffect(() => {
    dispatch(getNewsById(id));
    // eslint-disable-next-line
  }, []);
  const { news } = useSelector(({ infoReducer }) => infoReducer);
  const useStyles = makeStyles((theme) => ({
    newsTop: {
      height: 320,
      width: "100%",
      marginBottom: 40,
      objectFit: "cover",
    },
    newsTitle: {
      color: "#000",
      marginTop: 16,
      marginBottom: 32,
      maxWidth: 1090,
    },
    newsDescription: {
      color: "#111",
      lineHeight: "150%",
    },
  }));
  const classes = useStyles();
  return (
    <section className="whiteBg" style={{ paddingBottom: 80 }}>
      <img className={classes.newsTop} src={news?.detail?.photo} alt="top" />
      <Container>
        <Box display="flex">
          <img src={calendar} style={{ marginRight: 19 }} alt="calendar" />
          <Typography style={{ color: "#64748B", fontSize: 18 }}>
            {news?.detail?.date}
          </Typography>
        </Box>
        <Typography variant="h2" className={classes.newsTitle}>
          {news?.detail?.name}
        </Typography>

        <Typography
          variant="body1"
          className={classes.newsDescription}
          dangerouslySetInnerHTML={{
            __html: news?.detail?.body,
          }}
        />
      </Container>
    </section>
  );
};

export default DetailNews;
